.header {
  min-height: 658px;
  margin-bottom: 112px;

  color: $color-default-white;

  background-color: $color-blue;
  background-image: url("../img/bg-intro-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include retina {
    background-image: url("../img/bg-intro-desktop@2x.jpg");
  }

  @include vp-1023 {
    min-height: 638px;
    margin-bottom: 90px;

    background-image: url("../img/bg-intro-tablet.jpg");
    box-shadow: none;

    @include retina {
      background-image: url("../img/bg-intro-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    min-height: 500px;
    margin-bottom: 56px;

    background-image: url("../img/bg-intro-mobile.jpg");

    @include retina {
      background-image: url("../img/bg-intro-mobile@2x.jpg");
    }
  }
}

.header__top-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 30px 0;

  @include vp-1023 {
    padding: 15px 0;
  }

  @include vp-767 {
    padding: 20px 0;
  }
}

.header__wrapper {
  @include vp-767 {
    position: relative;
  }

  &--menu-opened {
    @include vp-767 {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100vh;

      background-color: $color-transparent;
    }
  }
}

.header__overlay {
  display: none;

  @include vp-767 {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;

    &--menu-opened {
      display: block;
    }
  }
}

.header__top-wrapper--menu-opened {
  @include vp-767 {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 89%;
    height: 100vh;
    padding: 20px 15px;

    background-color: $color-default-white;

    overflow-y: auto;

    scrollbar-width: thin;
    scrollbar-color: $color-default-white $color-blue;

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-track {
      background: $color-default-white;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $color-blue;
      border: 3px solid $color-blue;
    }
  }
}

.header__logo {
  display: block;
  margin-top: 5px;

  @include vp-1023 {
    margin-top: 15px;
  }

  @include vp-767 {
    margin-top: 0;
    margin-bottom: 36px;
  }
}

.header__logo svg {
  fill: $color-default-white;

  @include vp-1023 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}

.header__top-wrapper--menu-opened .header__logo svg {
  fill: $color-default-black;
}

.header__heading-wrapper {
  padding-left: 95px;

  @include vp-1023 {
    padding-left: 0;
  }
}

.header__heading {
  margin: 161px 0 12px;
  padding: 0;

  font-weight: 700;
  font-size: 54px;
  line-height: 66px;

  @include vp-1023 {
    margin-top: 196px;
    font-size: 50px;
    line-height: 61px;
  }

  @include vp-767 {
    margin-top: 71px;
    margin-bottom: 11px;

    font-size: 36px;
    line-height: 45px;
  }
}

.header__slogan {
  margin: 0 0 30px;
  padding: 0;

  font-size: 20px;
  line-height: 24px;

  @include vp-1023 {
    font-size: 18px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
  }
}
